body {
  margin: 0;

  padding: 0;
}

@font-face {
  font-family: "Neue Haas Unica";
  src: url("./assets/Fonts/NeueHaasUnica-Regular.woff2") format("woff2"),
    url("./assets/Fonts/NeueHaasUnica-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: "Neue Haas Unica", sans-serif;
  background-color: rgb(9, 29, 39);
}

.footer {
  margin-bottom: 2rem;
  width: 100%;
  text-align: center;
}

.container {
  margin: auto;
  width: 90%;
}

.container2 {
  width: 100%;
  position: absolute;
  top: 30%;

  margin: auto;
}
.container2 p {
  font-family: Neue Haas Unica;
  font-size: 70px;
  font-weight: 400;
  line-height: 70px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;

  text-align: center;
  color: white;
}

